
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'


import $ from "jquery";

export default {
name: "postOrder",
components: {
XHeader,
ViewBox,
FlexboxItem,
Flexbox,
Radio,
Group,
Popup,
XInput,
XSwitch,
XAddress,
XTextarea,
XButton,
Loading
},
data() {
return {

show: false,

isShowAddressList: false,

isShowAdd_Address: false,

addressData: ChinaAddressV4Data,

editAddress: {

address_id: "",

people: "",

phone: "",

location: [],

location_info: "",

is_default: false
},

goods_list: [],

payMethodList: ["在线支付", "微信"],

payMethod: "在线支付",

addressList: [],

defaultAddress: null
};
},

methods: {

goToPayment() {












var bag_id = [];
for(var i = 0; i < this.goods_list.length; i++) {
var item = this.goods_list[i];
bag_id.push(
item.bag_id
)
}
console.log(bag_id);

this.show = true;
var address_id = this.defaultAddress.address_id;

this.$post(
"order/add", {
bag_id: bag_id,
address_id: address_id

},
res => {
console.log(res);
this.show = false;
if(res = 1) {
this.$vux.toast.show({
text: "提交成功",
type: "text",
position: "middle"
});
}
if(res != 1) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}

}
);














},

update() {

this.$post(
"address/getList", {},
res => {
console.log(res);
this.addressList = res.msg;
}
);
},
delAddress(item, index, list) {
/*
if(confirm("确定删除吗？", "提示")) {
console.log("删除");
list.splice(index, 1);
if(item === this.defaultAddress) {
this.defaultAddress = {};
}
}
*/
this.$post("address/del", {
address_id: item.address_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.splice(index, 1);
this.$vux.toast.show({
text: "删除成功",
type: "text",
position: "middle"
});
}
if(res.res < 1) {
this.$vux.toast.show({
text: "删除失败",
type: "text",
position: "middle"
});
}

})
},

edit(item, index, list) {
var address_id = item.address_id;
var name = item.people;
var phone = item.phone;
var info = item.info;
var region = item.region;
this.$router.push({
path: "/addAddress/addAddress",
name: '/addAddress/addAddress',
params: {
openEdit: "1",
address_id: address_id,
name: name,
phone: phone,
info: info,
region: region
}
});

},

selectAddress(item) {
console.log("选择的", item)
for(let i = 0; i < this.addressList.length; i++) {
this.addressList[i].isActive = false;
}
var address_id = item.address_id
item.isActive = true;
this.defaultAddress = item;
},
getName(value) {
return value2name(value, ChinaAddressV4Data)
},

isPhone(str) {
var reg = /^0?1[3|4|5|8][0-9]\d{8}$/;
return reg.test(str);
return true;
},

saveAddress(editAddress) {
var name = this.editAddress.people;
var phone = this.editAddress.phone;
var address = this.getName(this.editAddress.location);
var info = address + this.editAddress.location_info;
if(name.length <= 0) {
this.$vux.toast.show({
text: "请输入姓名",
type: "text",
position: "middle"
});
return false;
}
if(phone.length <= 0) {
this.$vux.toast.show({
text: "请输入手机号",
type: "text",
position: "middle"
});
return false;
}
if(!this.isPhone(phone)) {
this.$vux.toast.show({
text: "请输入正确手机号",
type: "text",
position: "middle"
});
return false;
};
if(address.length <= 0) {
this.$vux.toast.show({
text: "请选择地址",
type: "text",
position: "middle"
});
return false;
};
if(this.editAddress.location_info.length <= 0) {
this.$vux.toast.show({
text: "请输入详细地址",
type: "text",
position: "middle"
});
return false;
};
if(this.editAddress.is_default == true) {
var is_default = "1"
} else {
var is_default = "0"
}

this.$post(
"address/add", {
add: {
people: name,
phone: phone,
region: address,
info: info,
is_default: is_default
}
},
res => {
console.log(res);
if(res.res >= 1) {
this.$vux.toast.show({
text: "保存成功",
type: "text",
position: "middle"
});

this.isShowAdd_Address = false;
}
if(res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
}

}
);








































}
},
mounted() {
this.update();
if(!this.$route.params["order"]) {
this.$router.back(-1);
return;
}












this.goods_list = this.$route.params.order.goods_list;
this.$nextTick(() => {});
},

watch: {},
computed: {
total() {
var money = 0;
for(let i = 0; i < this.goods_list.length; i++) {
let item = this.goods_list[i];
money += item.user_spec.money * item.goods_count;
}
money = money.toFixed(2);
this.totalMoney = money;
return money;
},

isAddressEmpty() {
if(this.addressList.length <= 0) {
return true;
}
for(let i = 0; i < this.addressList.length; i++) {
if(this.addressList[i].is_default) {
this.defaultAddress = this.addressList[i];
return false;
}
}
return true;
}
}
};



